}

.bg-bubbles{position:absolute;top:0;left:0;width:100%;height:100%;opacity:.2;z-index:-1}.bg-bubbles li{position:absolute;list-style:none;display:block;width:40px;height:40px;background-color:rgba(255,255,255,.15);bottom:-400px;-webkit-animation:square 45s infinite;animation:square 45s infinite;transition-timing-function:linear}.bg-bubbles li:nth-child(1){left:10%}.bg-bubbles li:nth-child(2){left:20%;width:80px;height:80px;-webkit-animation-delay:4s;animation-delay:4s;-webkit-animation-duration:27s;animation-duration:27s}.bg-bubbles li:nth-child(3){left:25%;-webkit-animation-delay:8s;animation-delay:8s}.bg-bubbles li:nth-child(4){left:40%;width:60px;height:60px;-webkit-animation-duration:32s;animation-duration:32s;background-color:rgba(255,255,255,.15)}.bg-bubbles li:nth-child(5){left:70%}.bg-bubbles li:nth-child(6){left:80%;width:120px;height:120px;-webkit-animation-delay:6s;animation-delay:6s;background-color:rgba(255,255,255,.2)}.bg-bubbles li:nth-child(7){left:32%;width:160px;height:160px;-webkit-animation-delay:14s;animation-delay:14s}.bg-bubbles li:nth-child(8){left:55%;width:20px;height:20px;-webkit-animation-delay:30s;animation-delay:30s;-webkit-animation-duration:80s;animation-duration:80s}.bg-bubbles li:nth-child(9){left:25%;width:10px;height:10px;-webkit-animation-delay:4s;animation-delay:4s;-webkit-animation-duration:80s;animation-duration:80s;background-color:fade(white, 30%)}.bg-bubbles li:nth-child(10){left:90%;width:160px;height:160px;-webkit-animation-delay:22s;animation-delay:22s}.bg-bubbles li:nth-child(11){border-radius:20px;left:15%}.bg-bubbles li:nth-child(12){left:30%;border-radius:40px;width:80px;height:80px;-webkit-animation-delay:6s;animation-delay:6s;-webkit-animation-duration:30s;animation-duration:30s}.bg-bubbles li:nth-child(13){left:35%;border-radius:20px;-webkit-animation-delay:12s;animation-delay:12s}.bg-bubbles li:nth-child(14){left:35%;width:60px;height:60px;border-radius:30px;-webkit-animation-duration:36s;animation-duration:36s;-webkit-animation-delay:2s;animation-delay:2s;background-color:rgba(255,255,255,.15)}.bg-bubbles li:nth-child(15){left:35%;border-radius:40px}.bg-bubbles li:nth-child(16){left:40%;width:120px;height:120px;border-radius:60px;-webkit-animation-delay:14s;animation-delay:14s;background-color:rgba(255,255,255,.25)}.bg-bubbles li:nth-child(17){left:22%;width:160px;border-radius:80px;height:160px;-webkit-animation-delay:6s;animation-delay:6s}.bg-bubbles li:nth-child(18){left:52%;width:20px;height:20px;border-radius:10px;-webkit-animation-delay:24s;animation-delay:24s;-webkit-animation-duration:40s;animation-duration:40s}.bg-bubbles li:nth-child(19){left:25%;width:10px;height:10px;border-radius:5px;-webkit-animation-delay:4s;animation-delay:4s;-webkit-animation-duration:80s;animation-duration:80s;background-color:rgba(255,255,255,.35)}.bg-bubbles li:nth-child(10){left:90%;width:160px;border-radius:80px;height:160px;-webkit-animation-delay:22s;animation-delay:22s}@-webkit-keyframes square{0%{transform:translateY(0)}100%{transform:translateY(-1500px) rotate(600deg)}}@keyframes square{0%{transform:translateY(0)}100%{transform:translateY(-3000px) rotate(600deg)}}
.btn{line-height:50px;height:50px;text-align:center;width:250px;cursor:pointer}.btn-one{color:#fff;transition:all .3s;position:relative}.btn-one span{transition:all .3s}.btn-one::before{content:"";position:absolute;bottom:0;left:0;width:100%;height:100%;z-index:1;opacity:0;transition:all .3s;border-top-width:1px;border-bottom-width:1px;border-top-style:solid;border-bottom-style:solid;border-top-color:rgba(255,255,255,.5);border-bottom-color:rgba(255,255,255,.5);transform:scale(0.1, 1)}.btn-one:hover span{letter-spacing:2px}.btn-one:hover::before{opacity:1;transform:scale(1, 1)}.btn-one::after{content:"";position:absolute;bottom:0;left:0;width:100%;height:100%;z-index:1;transition:all .3s;background-color:rgba(255,255,255,.1)}.btn-one:hover::after{opacity:0;transform:scale(0.1, 1)}.btn-two{color:#fff;transition:all .5s;position:relative}.btn-two span{z-index:2;display:block;position:absolute;width:100%;height:100%}.btn-two::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;z-index:1;transition:all .5s;border:1px solid rgba(255,255,255,.2);background-color:rgba(255,255,255,.1)}.btn-two::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;z-index:1;transition:all .5s;border:1px solid rgba(255,255,255,.2);background-color:rgba(255,255,255,.1)}.btn-two:hover::before{transform:rotate(-45deg);background-color:rgba(255,255,255,0)}.btn-two:hover::after{transform:rotate(45deg);background-color:rgba(255,255,255,0)}.btn-three{color:#fff;transition:all .5s;position:relative}.btn-three::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;z-index:1;background-color:rgba(255,255,255,.1);transition:all .3s}.btn-three:hover::before{opacity:0;transform:scale(0.5, 0.5)}.btn-three::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;z-index:1;opacity:0;transition:all .3s;border:1px solid rgba(255,255,255,.5);transform:scale(1.2, 1.2)}.btn-three:hover::after{opacity:1;transform:scale(1, 1)}
*{box-sizing:border-box;margin:0;padding:0}a{text-decoration:none;color:#fff}body{font-family:"Source Sans Pro",sans-serif;color:#fff;font-size:1.3rem}body ::-webkit-input-placeholder{font-family:"Source Sans Pro",sans-serif;color:#fff}body :-moz-placeholder{font-family:"Source Sans Pro",sans-serif;color:#fff;opacity:1}body ::-moz-placeholder{font-family:"Source Sans Pro",sans-serif;color:#fff;opacity:1}body :-ms-input-placeholder{font-family:"Source Sans Pro",sans-serif;color:#fff;font-weight:300}.App{background:#3b4252;width:100%;height:100%;background:linear-gradient(to bottom right, #2E3440 0%, #434C5E 100%);overflow:hidden;margin:0 auto;position:absolute;padding:1em 2em 2em;z-index:-2}.container{max-width:1200px;margin:0 auto}.nav-list{list-style-type:none}.nav-list .btn{margin:0 auto}.nav-list li{margin-bottom:.2em}@media screen and (min-width: 600px){.nav-list{display:flex;justify-content:space-around}h1{font-size:3em}}h1{text-align:center;font-family:"Aldrich",Roboto,Arial,Helvetica,sans-serif;text-transform:uppercase;transition-duration:1s;transition-timing-function:ease-in-put;font-weight:200;line-height:2}
.note{margin-top:1em;font-size:.8em;text-align:center}@media screen and (min-width: 600px){.nav-download{margin-top:6em}}.instructions{margin:1em auto;margin-top:4em;width:30em}.instructions code{display:block;font-family:monospace;white-space:pre;margin:3em 0 3em 1em}
